<template>
    <div class="observer"/>
</template>

<script>
    export default {
        name: "observer",
        data() {
            return {
                observer: null
            }
        },
        mounted() {
            this.observer = new IntersectionObserver(([entry]) => {
                if (entry && entry.isIntersecting) {
                    this.$emit('intersect');
                }
            });

            this.observer.observe(this.$el);
        }
    }
</script>

